.more-about {
    background-color: #000;
    padding: 3rem 6rem;
}

.more-about h2 {
    margin-bottom: var(--bottom-margin);
    color: chartreuse;
    text-align: center;
}

.more-about p {
    line-height: var(--line-height);
    color: aliceblue;
    padding: 0.4rem;
}


.skill_main .skill_item {
    margin-top: 20px;
    margin-bottom: 18px;
}

.skill_main .skill_item .progress_br {
    border: 1px solid #eeeeee;
    padding: 3px;
    border-radius: 10px;
}

.skill_main .skill_item h4 {
    text-align: none;
    font-size: 16px;
    font-family: "Outfit", sans-serif;
    font-weight: 500;
    color: #cad4cb;
    margin-bottom: -10px;
}

.skill_main .skill_item .progress {

    background: transparent;
    height: 10px;
    border-radius: 10px;
}

.progress {
    display: flex;
    height: 3rem;
    overflow: hidden;
    font-size: 0.75rem;
    background-color: #e9ecef;
    border-radius: 0.25rem;
}

.progress-bar {
    display: flex;
    flex-direction: row;
    justify-content: center;
    color: #fff;
    text-align: center;
    white-space: nowrap;
    background-image: linear-gradient(0deg, #0342b6 0%, #88f3ff 100%);
    transition: width 0.6s ease;
}

.fa {
    display: inline-block;
    font: normal normal normal 14px/1 FontAwesome;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}


@media screen and (max-width: 720px) {
    /*changes reflects on screen with a width of 720px and below*/

    .more-about {
        margin-top: 2rem;
        padding: 1rem 3.5rem;
    }

    .more-about h2 {
        text-align: center;
        width: max-content;
        padding-left: 25px;
    }

    .more-about p {
        text-align: justify;
        width: 497px;
        padding-left: 26px;
    }

    .skill_main{
        text-align: center;
        width: 520px;

    }

}